.title {
    background-color: orangered;
    color: white;
    padding: 50px;
    text-align: center;
}

.btn {
    padding: 15px 40px;
    background-color: white;
    color: #262626;
    border: .9px solid white;
    border-radius: 1px;
    cursor: pointer;
    transition: all .4s;
}

.btn:hover{
    background-color: transparent;
    transition: all .4s;
    color: white;
}